import React, { Component } from 'react';
import { PlainElement } from './PlainElement.js';
var qs = require('qs');

String.prototype.capitalize = function() {
    return this.charAt(0).toUpperCase() + this.slice(1);
}

export class RehersalDinner extends Component {
    render() {
        let RehersalDinnerContent = 
            <div className="RehersalDinner">
                <p className="sub"><em>Friday, August 2, 2019</em></p>

                <p>Hello Bridal party! We are getting super close to the day of the wedding.</p>
                <p>Thank you all for playing a special part in our special day. As part of our celebration,
                    we request that you also attend our rehersal day. There we will cover the order of the ceremony
                    including the order for when we all march on the aisle, the ceremony order, and also what each of
                    you needs to do day-of the wedding.
                </p>
                <p>Below are the times that you need to know.</p>

                <div className="table-wrapper">
                    <table className="table">
                        <tr>
                            <td>4:00 pm</td>
                            <td>Ceremony Rehersal</td>
                            <td>St. Albert the Great Parish</td>
                        </tr>

                        <tr>
                            <td>6:00 pm</td>
                            <td>Rehersal Dinner</td>
                            <td>Deerfoot Inn &amp; Casino</td>
                        </tr>
                    </table>
                </div>
            </div>

        let WeddingEntourageContent =
            <div className="BridalParty">
                <p className="sub">Here's the official list of our wedding entourage and everybody involved in our wedding ceremony.</p>

                <strong>George's Parents</strong>
                <ul className="list">
                    <li>Samy Antonious</li>
                    <li>Christine Antonious</li>
                </ul>

                <strong>Jessica's Parents</strong>
                <ul className="list">
                    <li>Nicky Prieto</li>
                    <li>Maricor Prieto</li>
                </ul>

                <strong>Principal Sponsors (Godparents)</strong>
                <ul className="list">
                    <li>Bob Prieto</li>
                    <li>Hannah Prieto</li>
                    <li>Hany Youakim</li>
                    <li>Lina Girgis</li>
                </ul>

                <strong>Best Man</strong>
                <ul className="list">
                    <li>Danny Khadour</li>
                </ul>

                <strong>Maid-of-Honor</strong>
                <ul className="list">
                    <li>Nicole Prieto</li>
                </ul>

                <strong>Flower Girl</strong>
                <ul className="list">
                    <li>Anna Youakim</li>
                </ul>

                <strong>Ring and Coin Bearer</strong>
                <ul className="list">
                    <li>Jacob Jouaquin</li>
                </ul>

                <strong>Bridesmaids and Groomsmen</strong>
                <p><em className="list-sub">(also the official pairings for the aisle)</em></p>
                <ul className="list">
                    <li>Nicko Prieto <span className="and">and</span> Justine Prieto</li>
                    <li>Mina Youakim <span className="and">and</span> Catherine Antonious</li>
                    <li>Sam Jawich <span className="and">and</span> Danielle Prieto</li>
                    <li>Ryan Huarn <span className="and">and</span> Katrina Prieto</li>
                    <li>Thomas Fenton, Curtis Malainey <span className="and">and</span> Liza Kharlova</li>
                    <li>Steven Boddez, David Skrundz <span className="and">and</span> Andrea Garrovillo</li>
                </ul>

                <strong>Wedding Ceremony Leaders</strong>
                <ul className="list">
                    <li><span className="leader">Mass Celebrants: </span> Fr. Chris and Dcn. Ricardo Santiago</li>
                    <li><span className="leader">First Reading:</span> Caleb Lagrange</li>
                    <li><span className="leader">Responsorial Psalm:</span> Paulo Intal</li>
                    <li><span className="leader">Second Reading: </span> Mary Youakim</li>
                    <li> &nbsp; </li>
                    <li><span className="leader">Candle Ceremony: </span> Nicko Prieto <span className="and">and</span> Justine Prieto</li>
                    <li><span className="leader">Veil Ceremony: </span> Mina Youakim <span className="and">and</span> Catherine Antonious</li>
                    <li><span className="leader">Cord Ceremony: </span> Sam Jawich <span className="and">and</span> Danielle Prieto</li>
                </ul>
                
            </div>

        return (
          <div className="App">
              <div className="Header">
                <PlainElement
                    pid="rehersal"
                    title="Rehersal Day"
                    content={RehersalDinnerContent}
                    border="true"/>

                <PlainElement
                    pid="bridalParty"
                    title="the Wedding Entourage"
                    content={WeddingEntourageContent}
                    border="true"/>
                </div>
          </div>
        );
      }
}


